"use client";

import React, { useState, useEffect } from "react";
import { MapPin, Camera, History, LogOut, Clock } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";

import { pontoStorage, PontoRecord } from "@/lib/storage";

interface HistoryItem {
  type: string;
  time: string;
  status: string;
  variant: "success" | "warning" | "destructive";
}

export default function MobilePontoPage() {
  const [currentTime, setCurrentTime] = useState<Date | null>(null);
  const [location, setLocation] = useState("Localizando...");
  const [history, setHistory] = useState<HistoryItem[]>([]);

  useEffect(() => {
    // Evitar erro de hidrata√ß√£o setando a data apenas no cliente
    setCurrentTime(new Date());
    
    // Carrega hist√≥rico inicial
    const records = pontoStorage.getAll();
    if (records.length === 0) pontoStorage.seed();
    updateHistory();

    const timer = setInterval(() => {
      setCurrentTime(new Date());
    }, 1000);

    // Simula√ß√£o de geolocaliza√ß√£o
    setTimeout(() => {
      setLocation("Av. Paulista, 1000 - S√£o Paulo, SP");
    }, 2000);

    return () => clearInterval(timer);
  }, []);

  const updateHistory = () => {
    const records = pontoStorage.getAll().slice(0, 3);
    setHistory(records.map(r => ({
      type: r.type,
      time: new Date(r.timestamp).toLocaleTimeString("pt-BR", { hour: '2-digit', minute: '2-digit' }),
      status: r.status === 'ok' ? 'Aprovado' : r.status === 'pending' ? 'Pendente' : 'Atraso',
      variant: r.status === 'ok' ? 'success' : r.status === 'pending' ? 'warning' : 'destructive'
    })));
  };

  const playSuccessSound = () => {
    try {
      const AudioContext = window.AudioContext || (window as any).webkitAudioContext;
      if (!AudioContext) return;
      
      const audioCtx = new AudioContext();
      const oscillator = audioCtx.createOscillator();
      const gainNode = audioCtx.createGain();

      oscillator.connect(gainNode);
      gainNode.connect(audioCtx.destination);

      // Efeito de "ding" agrad√°vel
      oscillator.type = "sine";
      oscillator.frequency.setValueAtTime(800, audioCtx.currentTime); // Come√ßa em 800Hz
      oscillator.frequency.exponentialRampToValueAtTime(1200, audioCtx.currentTime + 0.1); // Sobe para 1200Hz

      // Volume envelope (fade out r√°pido)
      gainNode.gain.setValueAtTime(0.3, audioCtx.currentTime);
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioCtx.currentTime + 0.5);

      oscillator.start();
      oscillator.stop(audioCtx.currentTime + 0.5);
    } catch (e) {
      console.error("Erro ao tocar som:", e);
    }
  };

  const handleRegister = (type: 'Entrada' | 'Sa√≠da' | 'Sa√≠da Almo√ßo' | 'Volta Almo√ßo') => {
    if (!currentTime) return;
    
    playSuccessSound(); // Toca o som de sucesso

    pontoStorage.add({
      userId: '1',
      userName: 'Jo√£o Silva',
      client: 'Google Brasil',
      type: type,
      status: 'pending', // Sempre pendente at√© aprova√ß√£o
      location: location
    });
    
    updateHistory();
    alert(`${type} registrada com sucesso!`);
  };

  // Formata√ß√£o de hora
  const formatTime = (date: Date) => {
    return date.toLocaleTimeString("pt-BR", {
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
    });
  };

  // Formata√ß√£o de data
  const formatDate = (date: Date) => {
    return date.toLocaleDateString("pt-BR", {
      weekday: "long",
      day: "numeric",
      month: "long",
    });
  };

  if (!currentTime) return null; // Ou um loading spinner

  return (
    <div className="min-h-screen bg-gray-50 pb-20">
      {/* Header Mobile */}
      <header className="bg-primary text-primary-foreground p-6 rounded-b-[2rem] shadow-lg relative overflow-hidden">
        <div className="relative z-10 flex justify-between items-start">
          <div>
            <p className="text-primary-foreground/80 text-sm">Ol√°, Jo√£o Silva</p>
            <h1 className="text-2xl font-bold mt-1">Bom trabalho! üëã</h1>
          </div>
          <Button variant="ghost" size="icon" className="text-primary-foreground hover:bg-primary/20">
            <LogOut className="h-5 w-5" />
          </Button>
        </div>
        
        {/* Rel√≥gio Principal */}
        <div className="mt-8 text-center mb-4">
          <p className="text-primary-foreground/80 text-sm capitalize">{formatDate(currentTime)}</p>
          <div className="text-5xl font-bold tracking-wider font-mono mt-2">
            {formatTime(currentTime)}
          </div>
        </div>

        {/* Badge de Localiza√ß√£o */}
        <div className="flex justify-center mb-4">
          <div className="inline-flex items-center gap-2 bg-black/20 backdrop-blur-sm px-3 py-1.5 rounded-full text-xs text-white">
            <MapPin className="h-3 w-3" />
            {location}
          </div>
        </div>
      </header>

      <main className="px-4 -mt-6 relative z-20 space-y-4 pb-24">
        {activeTab === 'ponto' && (
            <>
                {/* Card Principal de A√ß√£o */}
                <Card className="shadow-xl border-none">
                <CardContent className="p-6 flex flex-col items-center gap-4">
                    <div className="w-full h-32 bg-slate-100 rounded-lg flex items-center justify-center relative overflow-hidden group cursor-pointer border-2 border-dashed border-slate-300 hover:border-primary transition-colors">
                    <div className="absolute inset-0 bg-slate-200/50 flex items-center justify-center">
                        <span className="text-xs text-slate-500 font-medium">Mapa / Geofence</span>
                    </div>
                    {/* Mock de Mapa */}
                    <div className="absolute inset-0 opacity-20 bg-slate-300" />
                    </div>
                    
                    <div className="w-full grid grid-cols-2 gap-3">
                    <Button size="lg" className="w-full h-14 text-lg bg-green-600 hover:bg-green-700 shadow-green-200 shadow-lg" onClick={() => handleRegister('Entrada')}>
                        Entrada
                    </Button>
                    <Button size="lg" variant="destructive" className="w-full h-14 text-lg shadow-red-200 shadow-lg" onClick={() => handleRegister('Sa√≠da')}>
                        Sa√≠da
                    </Button>
                    </div>

                    <div className="flex items-center gap-2 text-xs text-muted-foreground mt-2">
                    <Camera className="h-3 w-3" />
                    <span>Selfie obrigat√≥ria neste posto</span>
                    </div>
                </CardContent>
                </Card>

                {/* Resumo do Dia */}
                <div className="grid grid-cols-2 gap-3">
                <Card>
                    <CardContent className="p-4 flex flex-col items-center justify-center text-center">
                    <span className="text-xs text-muted-foreground mb-1">Jornada</span>
                    <span className="font-bold text-lg">08:45h</span>
                    </CardContent>
                </Card>
                <Card>
                    <CardContent className="p-4 flex flex-col items-center justify-center text-center">
                    <span className="text-xs text-muted-foreground mb-1">Saldo</span>
                    <Badge variant="success" className="bg-green-100 text-green-700 hover:bg-green-200">+00:15h</Badge>
                    </CardContent>
                </Card>
                </div>

                {/* Hist√≥rico Recente */}
                <Card>
                <CardHeader className="pb-2">
                    <div className="flex items-center justify-between">
                    <CardTitle className="text-sm font-medium flex items-center gap-2">
                        <History className="h-4 w-4" />
                        √öltimos Registros
                    </CardTitle>
                    <Button variant="link" className="text-xs h-auto p-0">Ver todos</Button>
                    </div>
                </CardHeader>
                <CardContent>
                    <div className="space-y-4">
                    {history.map((item, index) => (
                        <div key={index} className="flex items-center justify-between py-2 border-b last:border-0">
                        <div className="flex items-center gap-3">
                            <div className={`w-2 h-2 rounded-full ${item.type.includes("Entrada") || item.type.includes("Volta") ? "bg-green-500" : "bg-red-500"}`} />
                            <div>
                            <p className="text-sm font-medium">{item.type}</p>
                            <p className="text-xs text-muted-foreground">Hoje</p>
                            </div>
                        </div>
                        <div className="text-right">
                            <p className="text-sm font-mono font-bold">{item.time}</p>
                            <Badge variant={item.variant as any} className="text-[10px] h-5 px-1.5">
                            {item.status}
                            </Badge>
                        </div>
                        </div>
                    ))}
                    {history.length === 0 && (
                        <div className="text-center text-muted-foreground text-sm py-4">
                        Nenhum registro hoje.
                        </div>
                    )}
                    </div>
                </CardContent>
                </Card>
            </>
        )}

        {activeTab === 'espelho' && (
            <div className="space-y-4 animate-in fade-in slide-in-from-bottom-4 duration-500">
                <Card>
                    <CardHeader>
                        <CardTitle className="text-lg flex justify-between items-center">
                            Espelho de Ponto
                            {mirror && <Badge className={mirror.status === 'APROVADO' ? 'bg-green-600' : 'bg-yellow-600'}>{mirror.status}</Badge>}
                        </CardTitle>
                        <p className="text-xs text-muted-foreground">M√™s Atual</p>
                    </CardHeader>
                    <CardContent className="space-y-4">
                        {loadingMirror ? (
                            <div className="text-center py-8">Carregando...</div>
                        ) : mirror ? (
                            <>
                                <div className="grid grid-cols-2 gap-4">
                                    <div className="bg-slate-50 p-3 rounded-lg">
                                        <p className="text-xs text-muted-foreground">Trabalhadas</p>
                                        <p className="text-xl font-bold text-green-700">{Number(mirror.workedHours).toFixed(2)}h</p>
                                    </div>
                                    <div className="bg-slate-50 p-3 rounded-lg">
                                        <p className="text-xs text-muted-foreground">Extras</p>
                                        <p className="text-xl font-bold text-orange-700">{Number(mirror.overtimeHours).toFixed(2)}h</p>
                                    </div>
                                    <div className="bg-slate-50 p-3 rounded-lg">
                                        <p className="text-xs text-muted-foreground">Faltas</p>
                                        <p className="text-xl font-bold text-red-700">{Number(mirror.absenceHours).toFixed(2)}h</p>
                                    </div>
                                    <div className="bg-slate-50 p-3 rounded-lg">
                                        <p className="text-xs text-muted-foreground">Atrasos</p>
                                        <p className="text-xl font-bold text-yellow-700">{Number(mirror.lateHours).toFixed(2)}h</p>
                                    </div>
                                </div>

                                <Button className="w-full" variant="outline" onClick={handleDownloadPdf}>
                                    <Download className="mr-2 h-4 w-4" />
                                    Baixar PDF Oficial
                                </Button>

                                {mirror.status !== 'APROVADO' ? (
                                    <Button className="w-full bg-green-600 hover:bg-green-700" onClick={handleConfirmMirror}>
                                        <CheckCircle className="mr-2 h-4 w-4" />
                                        Estou Ciente / Confirmar
                                    </Button>
                                ) : (
                                    <div className="bg-green-50 text-green-800 p-3 rounded text-center text-sm border border-green-200">
                                        <CheckCircle className="inline-block mr-1 h-4 w-4" />
                                        Espelho confirmado em {format(new Date(), 'dd/MM/yyyy')}
                                    </div>
                                )}
                            </>
                        ) : (
                            <div className="text-center py-8 text-muted-foreground">
                                <AlertTriangle className="mx-auto h-8 w-8 mb-2 opacity-50" />
                                N√£o foi poss√≠vel carregar o espelho.
                            </div>
                        )}
                    </CardContent>
                </Card>
            </div>
        )}
      </main>

      {/* Bottom Navigation */}
      <div className="fixed bottom-0 left-0 right-0 bg-white border-t py-2 px-6 flex justify-between items-center z-50">
        <div 
            className={`flex flex-col items-center gap-1 cursor-pointer ${activeTab === 'ponto' ? 'text-primary' : 'text-muted-foreground'}`}
            onClick={() => setActiveTab('ponto')}
        >
          <Clock className="h-6 w-6" />
          <span className="text-[10px] font-medium">Ponto</span>
        </div>
        <div 
            className={`flex flex-col items-center gap-1 cursor-pointer ${activeTab === 'espelho' ? 'text-primary' : 'text-muted-foreground'}`}
            onClick={() => setActiveTab('espelho')}
        >
          <FileText className="h-6 w-6" />
          <span className="text-[10px] font-medium">Espelho</span>
        </div>
        <div 
            className={`flex flex-col items-center gap-1 cursor-pointer ${activeTab === 'postos' ? 'text-primary' : 'text-muted-foreground'}`}
            onClick={() => setActiveTab('postos')}
        >
          <MapPin className="h-6 w-6" />
          <span className="text-[10px] font-medium">Postos</span>
        </div>
      </div>
    </div>
  );
}
